:root {
  --color-primary1: #092424;
  --color-primary2: #18675d;
  --color-secondary: #ffc367;
  --color-text-title: #ffd597;
  --color-card: #2b2b2b4b;
  --color-text: #fafafa;
  --color-body: #121212;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
@font-face {
  font-family: "SVN-Book Antiqua";
  src: url("./fonts/SVN-Book Antiqua.ttf") format("truetype");
}
@font-face {
  font-family: "SVN-Book Avo";
  src: url("./fonts/SVN-Avo.ttf") format("truetype");
}

a {
  text-decoration: none;
  color: var(--color-text);
}

a:hover {
  color: var(--color-text-title);
}

body {
  color: var(--color-text);
  font-family: "SVN-Book Avo";
  background-color: var(--color-body);
}

.fs__smallest {
  font-size: 12px;
}

.fs__details {
  font-size: 14px;
}

.fs__text {
  font-size: 18px;
}

.fs__title {
  font-size: 28px;
  font-family: "SVN-Book Antiqua", sans-serif;
  color: var(--color-text-title);
}

.fs__title--main {
  font-size: 42px;
  color: var(--color-text-title);
  font-family: "SVN-Book Antiqua", sans-serif;
}

.button-secondary {
  background-color: var(--color-secondary);
  color: black;
}

.button-secondary:hover {
  background-color: #ffc267d7;
  color: black;
}

.button-secondary--outline {
  color: var(--color-text);
  -webkit-box-shadow: 0 0 0 2px var(--color-secondary) inset;
  box-shadow: 0 0 0 2px var(--color-secondary) inset;
}

.button-secondary--outline:hover {
  background-color: var(--color-secondary);
  color: black;
}
.row {
  margin-left: 0;
  margin-right: 0;
}
.bg__custom--1 {
  background-image: url(./images/breadcrumbs-bg.jpg);
  background-size: cover;
  position: relative;
  z-index: -1;
}
.bg__custom--1::before {
  content: "";
  width: 100%;
  height: 70px;
  transform: rotate(180deg);
  background-image: url(./images/overlay-slider.webp);
  background-size: contain;
  position: absolute;
  z-index: 0;
  top: 0;
}
.bg__custom--1::after {
  content: "";
  width: 100%;
  height: 70px;
  background-image: url(./images/overlay-slider.webp);
  background-size: contain;
  position: absolute;
  z-index: 0;
  bottom: 0;
}

#loader-wrapper {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1000;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    /* Chrome, Opera 15+, Safari 3.1+ */
    /* IE 9 */
    transform: rotate(0deg);
    /* Firefox 16+, IE 10+, Opera */
  }
  100% {
    -webkit-transform: rotate(360deg);
    /* Chrome, Opera 15+, Safari 3.1+ */
    /* IE 9 */
    transform: rotate(360deg);
    /* Firefox 16+, IE 10+, Opera */
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    /* Chrome, Opera 15+, Safari 3.1+ */
    /* IE 9 */
    transform: rotate(0deg);
    /* Firefox 16+, IE 10+, Opera */
  }
  100% {
    -webkit-transform: rotate(360deg);
    /* Chrome, Opera 15+, Safari 3.1+ */
    /* IE 9 */
    transform: rotate(360deg);
    /* Firefox 16+, IE 10+, Opera */
  }
}

#loader-wrapper #loader {
  display: block;
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #3498db;
  -webkit-animation: spin 2s linear infinite;
  /* Chrome, Opera 15+, Safari 5+ */
  animation: spin 2s linear infinite;
  /* Chrome, Firefox 16+, IE 10+, Opera */
}

#loader-wrapper #loader:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #e74c3c;
  -webkit-animation: spin 3s linear infinite;
  /* Chrome, Opera 15+, Safari 5+ */
  animation: spin 3s linear infinite;
  /* Chrome, Firefox 16+, IE 10+, Opera */
}

#loader-wrapper #loader:after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #f9c922;
  -webkit-animation: spin 1.5s linear infinite;
  /* Chrome, Opera 15+, Safari 5+ */
  animation: spin 1.5s linear infinite;
  /* Chrome, Firefox 16+, IE 10+, Opera */
}
